* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list,
.country-inform {
  list-style: none;
  padding: 0;
}

li {
  display: flex;
  align-items: center;
}

.header-wrapper {
  display: flex;
  align-items: center;
}

.description {
  font-weight: bold;
}

img {
  display: block;
  margin-right: 10px;
}

h1,
p {
  margin: 0;
}

li:not(:last-of-type) {
  margin-bottom: 10px;
}
